.button-style-link-small-false-alternate-false-icon-position-trailing,
.button-style-link-small-false-alternate-false-icon-position-trailing * {
  box-sizing: border-box;
}
.button-style-link-small-false-alternate-false-icon-position-trailing {
  display: flex;
  flex-direction: row;
  gap: 8px;
  align-items: center;
  justify-content: center;
  position: relative;
}
.button-style-link-small-false-alternate-false-icon-position-trailing .button {
  color: var(--color-neutral-darkest, #000000);
  text-align: left;
  font-family: var(
    --text-regular-normal-font-family,
    "Roboto-Regular",
    sans-serif
  );
  font-size: var(--text-regular-normal-font-size, 16px);
  line-height: var(--text-regular-normal-line-height, 150%);
  font-weight: var(--text-regular-normal-font-weight, 400);
  position: relative;
}

